/* Core */
import { Component, ChangeDetectorRef, NgZone, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Rx';
import { IonicPage, NavController, NavOptions } from 'ionic-angular';
import { DomSanitizer } from '@angular/platform-browser';

/* Models */
import { ChannelWrapper } from '../../app/models/channel-wrapper.model';

/* Services */
import { UsersService } from '../../app/services/users.service';
import { FollowedChannel } from '../../app/models/followed-channel.model';
import { Channel } from '../../app/models/channel.model';
import { LocalStorageService } from '../../app/services/localStorage.service';

@IonicPage({
  name: 'following',
  segment: 'following'
})
@Component({
  selector: 'page-following',
  templateUrl: 'following.html'
})
export class FollowingPage implements OnInit {
  public followedChannels: FollowedChannel[] = [];
  public twitchUserId: string;

  constructor(
    public navCtrl: NavController,
    private usersService: UsersService,
    private ngZone: NgZone,
    private localStorageService: LocalStorageService) {}

  ngOnInit(): void {
    this.twitchUserId = this.localStorageService.getTwitchUserId();

    if (this.twitchUserId) {
        this.getFollowingChannels();
    }
  }

  getFollowingChannels() {
    let that = this;
    let promise;

    this.usersService.getFollowingChannelsById(that.twitchUserId).then(function(channelWrapper: ChannelWrapper) {
      that.ngZone.run(() => {
        that.followedChannels = channelWrapper.follows;

        that.followedChannels.sort(function(a: FollowedChannel, b: FollowedChannel) {
          return (b.channel.followers - a.channel.followers);
        })
      });
    });
  }
}



